












import Vue from "vue";
import * as tf from "@tensorflow/tfjs";
import DigitInput from '../components/DigitInput.vue';
import modelpath from '../assets/models/minst1/model.json'

export default Vue.extend({
  data: () => {
    return {
      model: <tf.LayersModel | null>null,
      info: "",
      digit: ""
    };
  },
  async mounted() {
    this.info = "";
    // console.log(require('file-loader!../assets/models/minst1/group1-shard1of1.bin'));
    // console.log(require('file-loader!../assets/models/minst1/model.json'));
    // console.log(modelpath)
    // let modelurl = location.origin + <string>require('../assets/models/minst1/model.json')
    let modelurl = modelpath;
    try{
      let model = await tf.loadLayersModel(modelurl);
      this.model = model;
      model.summary(undefined, undefined, msg => {
        this.info = this.info + msg + "\n";
      });
    }catch(e){
      console.log('Load Model Failed!!!');
    }
  },
  components:{
    DigitInput
  }
})
